@Injectable({
  providedIn: 'root'
})
export class SharedataService {
  private data;
  private modals: any[] = [];
  setData(data) {
    this.data = data;
  }

  getData() {
    let temp = this.data;
    this.clearData();
    return temp;
  }

  clearData() {
    this.data = undefined;
  }
  open(id: string) {
    // open modal specified by id
    let modal: any = this.modals.filter(x => x.id === id)[0];
    // modal.open();
}
}
