import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormArray, Validators } from '@angular/forms';
import { FormBuilder } from '@angular/forms';
import { matchOtherValidator } from './match-other-validator';
import { NoWhitespaceValidator } from '../validations/no-whitespace.validator';
import { Observable } from 'rxjs';
import { AuthService } from '../services/auth.service';

import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { HttpClient } from '@angular/common/http';


@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {
  myform: FormGroup;
  token: any;

  parentMessage = "message from parent";
  private headers: Headers = new Headers({ 'Content-Type': 'application/json' });
  userData: any;
  user: any = { email: "", password: "",mobileno:'',otp:'' };
  constructor(private toastr: ToastrService, private router: Router, private http: HttpClient, private auth: AuthService) {

  }
  ngOnInit() {


    this.myform = new FormGroup({
      email: new FormControl('', [Validators.required, Validators.minLength(3), Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$')]),
      password: new FormControl('', Validators.required),
      mobileno: new FormControl('', Validators.required),
      otp: new FormControl('', [Validators.required,Validators.minLength(4)])
    });
  }

  get email() {
    return this.myform.get('email');
  }

  get password() {
    return this.myform.get('password');
  }

  onLogin() {

    this.auth.login(this.user).then((data) => {

      console.log(data);

      if (data) {
        this.userData = data.json();
        localStorage.setItem('token', data.json().token);
        this.token = localStorage.getItem('token');
        console.log(this.token);

        this.toastr.success('Welcome Palak!!!');
        this.router.navigateByUrl('/home');

      }

    }).catch((err) => {
      console.log(err);
    });


  }
  getRandomNum(length) {
    var randomNum = 
        (Math.pow(10,length).toString().slice(length-1) + 
        Math.floor((Math.random()*Math.pow(10,length))+1).toString()).slice(-length);
        console.log(randomNum);
    return randomNum;
}
}
