import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators, FormBuilder } from "@angular/forms";
import { Router, ActivatedRoute, Params } from "@angular/router";
import { Http, Headers } from "@angular/http";
import { AuthService } from "../services/auth.service";
import { HttpClient } from "@angular/common/http"

import { ToastrService } from 'ngx-toastr';
import { HttpHeaders, HttpErrorResponse } from '@angular/common/http';
declare var $: any;
@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
    myform: FormGroup;
    // token=localStorage.getItem('token');
    token: any;
    data: any;
    isActive: boolean = false;
    parentMessage = "message from parent";
    private headers: Headers = new Headers({ 'Content-Type': 'application/json' });
    userData: any;
    user: any = { email: "", password: "" };
    signupData: any;
    signup: any = { name: "", email: "", password: "" };
    myformsignup: FormGroup;
    otpData: any;
    otpVerify: any;
    message: any;
    userId: any;
    Id: any;
    constructor(private formBuilder: FormBuilder, private activatedRoute: ActivatedRoute, private toastr: ToastrService, private router: Router, private http: HttpClient, private auth: AuthService) {

    }
    ngOnInit() {
        $(document).ready(function () {
            $('.tab a').on('click', function (e) {
                e.preventDefault();

                $(this).parent().addClass('active');
                $(this).parent().siblings().removeClass('active');

                var href = $(this).attr('href');
                $('.forms > form').hide();
                $(href).fadeIn(500);
            });
        });

        this.myform = new FormGroup({
            email: new FormControl('', [Validators.required, Validators.minLength(3), Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$')]),
            password: new FormControl('', Validators.required)
        });
        this.myformsignup = new FormGroup({
            name: new FormControl('', [Validators.required]),
            email: new FormControl('', [Validators.required]),
            password: new FormControl('', Validators.required),

        });
    }

    get email() {
        return this.myform.get('email');
    }

    get password() {
        return this.myform.get('password');
    }

    get email1() {
        return this.myformsignup.get('email');
    }

    get password1() {
        return this.myformsignup.get('password');
    }
    get name() {
        return this.myformsignup.get('name');
    }
    
    // Login Api binding function
    onLogin() {
       
        console.log("login", this.user);
        this.auth.login({ email: this.user.email, password: this.user.password }).then((result) => {

            console.log("Message", result.json().message);
            this.user = result.json();
            console.log(this.user.candi_fullname);
            localStorage.setItem('token', result.json().token);
            this.token = localStorage.getItem('token');
            this.userId = localStorage.getItem(this.user.id);
            console.log(this.userId);
            console.log("token =>", this.token);
            console.log(result.json());
            // this.toastr.success('Welcome!!!');


            this.router.navigateByUrl('/instruction');


        }).catch((err) => {
            // debugger;
            if (err.json().error === 'Email id not registered, please register first.') {
                this.toastr.error('Email id not registered, please register first.');
            }
            if (err.json().error === "Authantication failed please enter correct password") {
                this.toastr.error('Authantication failed please enter correct password');
                
            }
            if (err.json().error === "Authantication failed please check password") {
                this.toastr.error('Authantication failed please check password');
            }
            if (err.json().error === "Authantication failed") {
                this.toastr.error('Authantication failed');
            }
            console.log(err);
        });
    }
    //signup Api binding function
    Signup() {
        console.log(this.signup);
        this.auth.SignuppostDetail('/signup', { email: this.signup.email, password: this.signup.password, fullname: this.signup.name }).then((data) => {
            console.log(data);
            // debugger;
            if (data) {
                this.signup = data.json().message;
                console.log("dataSigup", this.signup.id);
                console.log("dataSigup", this.signup);
                this.toastr.success(this.signup);
                this.reset();
            }
            else {
                this.toastr.error('OTP is not verfied');
            }
        }).catch((err) => {
            if (err.json().error === 'User already exists.') {
                this.toastr.error('User already exists.');
            }
        });
    }
    otp() {
        if (this.signup.mobileno != '') {
            this.auth.postDetail('/candi_sendotp', this.token, { mobile_no: this.signup.mobileno }).then((data) => {
                this.data = data.json().result;
                this.otpData = data.json().message;
                console.log(this.otpData);
                this.Id = this.data.id;
                this.toastr.success("The OTP is sent to your registered Mobile Number");
            })
        }
        else {
            this.toastr.error('Please Enter Mobile Number');
        }

    }
    reset() {
        this.myformsignup.reset();
    }
}
