import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { Ng2FileSizeModule } from 'ng2-file-size';
import { AppComponent } from './app.component';
import { ParticlesModule } from 'angular-particle';
import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';

import { NgForm } from '@angular/forms';
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { RouterModule, Router } from "@angular/router";
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpModule } from "@angular/http";
import { SimpleNotificationsModule } from 'angular2-notifications';
import { AuthService } from "./services/auth.service";
import { GooglePlaceModule } from "ngx-google-places-autocomplete";
import { CommonModule } from '@angular/common';
import { ToastrModule, ToastContainerModule } from 'ngx-toastr';
import { HttpClientModule, HttpHeaders } from "@angular/common/http";
import { TabModule } from "angular-tabs-component";
import { SpeechModule } from "ngx-speech";
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { DatePipe } from '@angular/common'

import { TalkService } from './services/talk.service';
import { NgCircleProgressModule } from 'ng-circle-progress';
import { NgxPaginationModule } from 'ngx-pagination';
import { NoWhitespaceDirective } from './validations/no-whitespace.directive';
import { CountdownModule } from 'ngx-countdown';
import { ConfirmationPopoverModule } from 'angular-confirmation-popover';
import { BarRatingModule } from "ngx-bar-rating";
import { FileUploadModule } from 'ng2-file-upload';
import { ShowHidePasswordModule } from 'ngx-show-hide-password';

import { AuthGuardService } from "./services/auth-guard.service";
import { JwtModule } from '@auth0/angular-jwt';
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';

import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';

import { CanDeactivateGuard } from './services/deactivate-guard.service';
import { PopoverModule } from "ngx-popover";
import { AngularFileUploaderModule } from "angular-file-uploader";





import { SharedataService } from './services/sharedata.service';

import { SpeechtestreportComponent } from './speechtestreport/speechtestreport.component';
import { SpeechfeedbackComponent } from './speechfeedback/speechfeedback.component';
import { InstructionComponent } from './instruction/instruction.component';
import { StarttestComponent } from './starttest/starttest.component';
import { BlockCopyPasteDirective } from './services/block-copy-paste.directive';
import { ChangecolorDirective } from './services/changecolor.directive';


export function tokenGetter() {
    return localStorage.getItem('access_token');
}


@NgModule({
    declarations: [
        NoWhitespaceDirective,
        AppComponent,
        LoginComponent,
        SignupComponent,
   
        ForgotPasswordComponent,
   
   
     
        SpeechtestreportComponent,
        SpeechfeedbackComponent,
        InstructionComponent,
        StarttestComponent,
        BlockCopyPasteDirective,
        ChangecolorDirective,

    ],
    imports: [
        BrowserModule,
        FormsModule,
        CommonModule,
        BarRatingModule,
        HttpModule,
        AngularFileUploaderModule,
        FileUploadModule,
        TabModule,
        Ng2FileSizeModule,
        HttpClientModule,
        SpeechModule,
        GooglePlaceModule,
        PopoverModule,
        ReactiveFormsModule,
        SimpleNotificationsModule.forRoot(),
        ShowHidePasswordModule.forRoot(),
        ConfirmationPopoverModule.forRoot({
            confirmButtonType: 'danger' // set defaults here
        }),
        BsDatepickerModule.forRoot(),
       
        NgxPaginationModule,
        BrowserAnimationsModule,
        CountdownModule,
        ToastrModule.forRoot({
            timeOut: 1000,
            positionClass: 'toast-top-right',
            preventDuplicates: true,
        }),
        ToastContainerModule,
        ParticlesModule,
        NgMultiSelectDropDownModule,
        JwtModule.forRoot({
            config: {
                tokenGetter: tokenGetter,
                whitelistedDomains: ['localhost:4200'],
                blacklistedRoutes: ['localhost:4200/auth/'],

            }
        }),
        RouterModule.forRoot([
            {
                path: '',
                component: LoginComponent,
            },
            {
                path: 'login',
                component: LoginComponent,
            },
         
            {
                path: 'signup',
                component: SignupComponent,
            },
         
            {
                path: 'start/:id',

                component: StarttestComponent,
                canActivate:[AuthGuardService],
            },
            {
                path: 'instruction',
                component: InstructionComponent,
                   canActivate:[AuthGuardService]
            },
           
            {
                path: 'forgot',
                component: ForgotPasswordComponent,
                //   canActivate:[AuthGuardService]

            },
            
            {
                path: 'speechreport',
                component: SpeechtestreportComponent,
                canActivate:[AuthGuardService]
            },
            {
                path: 'feedback',
                component: SpeechfeedbackComponent,
                canActivate:[AuthGuardService]
            },
           


        ])
    ],
    providers: [AuthService, AuthGuardService, TalkService, CanDeactivateGuard, SharedataService, DatePipe,

        
        { provide: 'SPEECH_LANG', useValue: 'en-US' },
    ],
    bootstrap: [AppComponent]
})
export class AppModule { }
