import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AuthService } from '../services/auth.service';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-instruction',
  templateUrl: './instruction.component.html',
  styleUrls: ['./instruction.component.css']
})
export class InstructionComponent implements OnInit {
  myform: FormGroup;
  data = 50;
  submitButton: any;
  QuestionCount: any = 105;
  responseNextData: any = [];
  nextqueId = null;
  responseNextData1: any = [];
  skipques: any;
  collectskip: any = [];
  submitData: any = [];
  SkippedData: any = [];
  getData: any = [];
  msg = '';
  comment = '';
  context = '';
  subscription: Subscription;
  menu: any;
  token: any = localStorage.getItem('token');
  questions: any;
  good: any;
  p: number = 1;
  started = false;
  keystart: any = false;
  visible = false;
  candi_skillset: any;
  question_id = "M1";
  resData: any = [];
  responseData: any = [];
  /* clock variables */
  timeLeft: number = 100 * 27;
  // timeLeft: number = 150;
  d: number;
  counterTime: any;
  stoptime;
  interval;
  Marks = null;
  skipquestion:any;
  skipcount:any;
  constructor(private router: Router, private auth: AuthService, private toast: ToastrService) { }

  ngOnInit() {
    this.myform = new FormGroup({
      nextqueId: new FormControl(''),
      Marks: new FormControl(''),
    });
  }
  navigate() {
    var d = new Date().toLocaleString();
    console.log(d);  
    
    this.auth.postDetail('/start_test', this.token,{start_time:d}).then(data => {

      if(data.json().error == 'Assessment already Performed'){
        this.toast.warning('Assessment already Performed');  
      } else {
       this.router.navigateByUrl('/start/' + this.data);
      }


     });
    
  
    } 
}
