import { Component, OnInit } from '@angular/core';
import { SharedataService } from '../services/sharedata.service';
import { AuthService } from '../services/auth.service';
import { Params, ActivatedRoute, Router } from '@angular/router';
import { resolve } from 'url';
import { Observable, of } from 'rxjs';

@Component({
  selector: 'app-speechtestreport',
  templateUrl: './speechtestreport.component.html',
  styleUrls: ['./speechtestreport.component.css']
})
export class SpeechtestreportComponent implements OnInit {
  data: any;
  token: any;
  candi_skillset: any;
  candidateData: any = [];
  listusermarks: any = [];
  totalmarks: any = [];
  start_time:any;
   end_time:any;
  constructor(private share: SharedataService, private auth: AuthService, private activatedRoute: ActivatedRoute, private router: Router) {
    if (this.data) {
      // do whatever needed
      console.log(this.data);

    }
    else {
      // this.router.navigateByUrl('/sender');
      console.log("hello");
    }

  }

  ngOnInit() {
    
    this.token = localStorage.getItem('token');

    console.log(this.token);

    setTimeout(() => {
      this.auth.get('/assessment_result', this.token).then((list) => {
        this.candidateData = list.json();
         
         console.log("Candidate Info", this.candidateData);
        this.start_time=this.candidateData.user[0].start_time;
        this.end_time=this.candidateData.user[0].end_time;
        this.listusermarks = this.candidateData.user;
        this.totalmarks = this.candidateData.total;
        // console.log("Candidate Info", this.totalmarks);
        // console.log("Candidate Info", this.listusermarks);

      }).catch((err) => { });
    }, 1000);

  }
  logout() {
    // this.auth.get('/report_xlxs',this.token).then((data)=>{

    // })  
      //  this.auth.get('/candi_logout', this.token);
      localStorage.removeItem('token');
      this.router.navigateByUrl('/');
    //  });
   }
  
}
async function delay(ms: number) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

