  startTimerHHMMSS(d) {
    d = Number(d);
    var h = Math.floor(d / 3600);
    var m = Math.floor(d % 3600 / 60);
    var s = Math.floor(d % 3600 % 60);

    var hDisplay = h > 0 ? h + (h === 1 ? "" : "") : "";
    var mDisplay = m > 0 ? m + (m === 1 ? "" : "") : "";
    var sDisplay = s > 0 ? s + (s === 1 ? "" : "") : "";
    this.counterTime = this.GetPaddedIntString(hDisplay, 2) + ":" + this.GetPaddedIntString(mDisplay, 2) + ":" + this.GetPaddedIntString(sDisplay, 2);

    return this.counterTime;
  }
  startTimer() {
    if (!this.interval) {
      this.interval = setInterval(() => {
        if (this.timeLeft === 0) {
          this.route.navigateByUrl('/feedback');
        }
        if (this.timeLeft >= 0) {
          this.startTimerHHMMSS(this.timeLeft);
          this.timeLeft--;
        }
        // if (this.timeLeft == 301) {
        //   return this.toast.error('You have only 5 mins left!');
        // }
        else if (this.timeLeft < 120) {
          this.startTimerHHMMSS(this.timeLeft);
          this.timeLeft--;
        }
      }, 1000);
    }
  }
  pauseTimer() {
    clearInterval(this.interval);
  }

  GetPaddedIntString(n, numDigits) {
    var nPadded = n;
    for (; nPadded.length < numDigits;) {
      nPadded = "0" + nPadded;
    }
    return nPadded;
  }
