import { Component, OnInit } from '@angular/core';
import { AuthService } from '../services/auth.service';
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { ToastrService } from 'ngx-toastr';
import { Message } from '@angular/compiler/src/i18n/i18n_ast';
import { SharedataService } from '../services/sharedata.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {
  forgotPassword: any = { to: "", otp: "" };

  myform: FormGroup;
  accesstoken: any;
  token = localStorage.getItem('token');
  data: any;
  otpData: any;
  otpVerify:any;
  remove:any='otp is not correct';
  constructor(private auth: AuthService, private toastr: ToastrService,private share:SharedataService,private router:Router) { }

  ngOnInit() {
    this.myform = new FormGroup({
      email: new FormControl('', [Validators.required, Validators.minLength(3), Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$')]),
      otp: new FormControl('', [Validators.required, Validators.maxLength(4),Validators.pattern("^[0-9]*$"),]),
    });

  }
  proceed(){
  //  debugger;
  console.log(this.forgotPassword);
  console.log(this.accesstoken);
    this.auth.postDetail('/verify_password', this.accesstoken, { email: this.forgotPassword.to, email_otp: this.forgotPassword.otp })
        .then((data) => {
            // console.log("email", this.data.candi_email);
            this.otpVerify = data.json().message;
            console.log(this.otpVerify);
            // if (this.otpVerify == "OTP matched successfuly") {
              this.share.setData(data);
              console.log("this data print",data);
              this.router.navigateByUrl('/newpassword');
            // }
            // else{
            //   this.toastr.remove(this.remove);
            // }
        }).catch((err)=>{console.log(err.json().message);
          if (err.json().error === 'Failed to authenticate token.') {
            this.toastr.error('Please enter correct OTP');
        }});
  }
  otp() {
    this.auth.postDetail('/forgot_password', " ", { email: this.forgotPassword.to }).then((data) => {
      this.accesstoken = data.json().token;
      console.log("Response token for forget password " + this.accesstoken);
      localStorage.setItem("token", this.accesstoken);
      this.otpData = data.json().message;
      console.log(this.otpData);
      // this.Id = this.data.id;
      this.toastr.success(this.otpData);
    }).catch((err) => {
      console.log(err);
    });
  }

}
