import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { SharedataService } from '../services/sharedata.service';
import { AuthService } from '../services/auth.service';
// import { ResultTestComponent } from '../Dashboard/result-test/result-test.component';


@Component({
  selector: 'app-speechfeedback',
  templateUrl: './speechfeedback.component.html',
  styleUrls: ['./speechfeedback.component.css']
})
export class SpeechfeedbackComponent implements OnInit {
  give_star: any;
  give_comment: any;
  myform: FormGroup;
  token =localStorage.getItem('token');
  data:any;
  result:any;
  candi_id:any;
  user_name:any;
  constructor(private router:Router,private share: SharedataService,private auth:AuthService) { }

  ngOnInit() {
     
    this.auth.get('/candi_details',this.token).then((data)=>{
      this.result =data.json().user;
     this.candi_id=this.result.candi_id;
     this.user_name=this.result.candi_fullname;
    })
    this.myform = new FormGroup({
      give_star: new FormControl(),
      give_comment: new FormControl()
    });
  }
  done(){
    
    var d=new Date().toLocaleString();
       console.log(d);
       this.auth.postDetail('/start_test', this.token,{end_time:d}).then(data => {
       
      })
    
     console.log(this.candi_id,this.user_name);
    
    this.auth.postDetail('/report_xlxs',this.token,{candi_id:this.candi_id,user_name:this.user_name}).then((report) => {
    
    })
  
      this.router.navigateByUrl('/speechreport');
  }
  // }
}
