
import { Router } from '@angular/router';
import { EventEmitter, Injectable, Output } from '@angular/core';
import { Headers, Http } from '@angular/http';
import { JwtHelperService } from '@auth0/angular-jwt';
import 'rxjs';
import { HttpClientModule, HttpClientJsonpModule, HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from "rxjs/internal/Observable";

@Injectable()
export class AuthService {
    //private BASE_URL: string = 'http://localhost:3003'; 
	 private BASE_URL: string = 'http://18.223.249.160:3003';   // api url
    //private BASE_URL: string = 'http://52.14.69.51:3003';   // api url
    private Speech: string = ' http://13.233.39.240:3002/getfirstquestion';   // speech api url
    private evaluate: string = ' http://13.233.39.240:3002';   // speech api url
    private NextSpeech: string = 'http://13.233.39.240:3002/getnextquestion';   // speech api url
    private URL: string = 'http://localhost:1997/api/candiusers/forgot';   //Forgot Passwword
    //token :any;
    public Authid = localStorage.getItem('Authid');
    public token = localStorage.getItem('token');
    public candi_id = localStorage.getItem('candi_id');
    public user_name = localStorage.getItem('user_name');
    private loggedIn: boolean = false;
    private headers: Headers = new Headers({ 'Content-Type': 'application/json' });  // For CORS HTTP

    // private selectedFile: File = null;
    // private fb: any = new FormData();

    constructor(public jwtHelper: JwtHelperService, private http: Http, private router: Router) {
        if (localStorage.getItem('token')) {
            this.headers.set('x-access-token', `${localStorage.getItem('token')}`); // Set The Token And Authorization
        }
        if (localStorage.getItem('Authid')) {
            this.headers.set('Authid', `${localStorage.getItem('Authid')}`); // Set The Token And Authorization
            console.log(this.Authid);
        }
        if (localStorage.getItem('candi_id')) {
            this.headers.set('candi_id', `${localStorage.getItem('Authid')}`); // Set The Token And Authorization
            console.log(this.candi_id);
        }
        if (localStorage.getItem('user_name')) {
            this.headers.set('user_name', `${localStorage.getItem('Authid')}`); // Set The Token And Authorization
            console.log(this.user_name);
        }
    }


    // Forgot Password
    forgot(user: any): Promise<any> {
        console.log(user);
        let url: string = `${this.URL}/password`;
        return this.http.post(url, user, { headers: this.headers }).toPromise();
    }

    menuList(p1, Authid): Promise<any> {
        let url: string = `${this.BASE_URL}` + p1;
        let headers: Headers = new Headers({
            'Content-Type': 'application/json',
            'Authid': `${localStorage.getItem('Authid')}`,

        });
        return this.http.post(url, { headers: headers }).toPromise();
    }

    // For Login And Authenticate
    login(user: any): Promise<any> {
        
        console.log(user);
        let url: string = `${this.BASE_URL}/signin`;
        return this.http.post(url, user, { headers: this.headers }).toPromise();
    }



    // check the authentication and token
    public isAuthenticated(): boolean {
        const token = localStorage.getItem('token');
        console.log(token);
        return !this.jwtHelper.isTokenExpired(token);
    }

    // get and Post Data (Both)
    async getList(p1, method = 'get', data: any = ''): Promise<any> {
        let url: string = `${this.BASE_URL}` + p1;
        let headers: Headers = new Headers({
            ' Content-Type': 'application/json',
            // 'x-access-token': `${token}`
        });
        if (method == 'get') {
            return this.http.get(url, { headers: headers }).toPromise().then(res => {
                return res;
            }).catch(error => this.handleError(error));
        } else {
            return this.http.post(url, data, { headers: headers }).toPromise().then(res => {
                return res;
            }).catch(error => this.handleError(error));
        }
    }


    // store the data
    get(p1, token): Promise<any> {
        let url: string = `${this.BASE_URL}` + p1;
        let headers: Headers = new Headers({
            'Content-Type': 'application/json',
            'x-access-token': `${localStorage.getItem('token')}`,
        });
        return this.http.get(url, { headers: headers }).toPromise();
    }
    // update for update
    update(p1, token, data: any = ''): Promise<any> {
        let url: string = `${this.BASE_URL}` + p1;
        let headers: Headers = new Headers({
            'Content-Type': 'application/json',
            'x-access-token': `${token}`
        });
        return this.http.post(url, data, { headers: headers }).toPromise();
    }

    //     //upload for 
    //     onUploadImg(p1, fb: any = '', token): any {
    //         let url: string = `${this.BASE_URL}` + p1;
    //         const httpHeader = new HttpHeaders().set('x-access-token', `${token}`);
    //     return this.httpClient.post(url, fb).subscribe(err => {
    //         console.log(err);
    //     });
    // }

    // for get the data
    postDetail(p1, token, data: any = ''): Promise<any> {
        let url: string = `${this.BASE_URL}` + p1;
        let headers: Headers = new Headers({
            'Content-Type': 'application/json',
            'x-access-token': `${token}`,
        });
        return this.http.post(url, data, { headers: headers }).toPromise();
    }
    

    //speech post data
    postspeech(data: any = ''): Promise<any> {
        let url: string = `${this.Speech}`;
        let headers: Headers = new Headers({
            'Content-Type': 'application/json'
        });
        return this.http.post(url, data, { headers: headers }).toPromise();
    }
    //speech post data
    postspeechEvaluate(data: any = ''): Promise<any> {
        let url: string = `${this.evaluate}`;
        let headers: Headers = new Headers({
            'Content-Type': 'application/json'
        });
        return this.http.post(url, data, { headers: headers }).toPromise();
    }
    //speech post data Next
    postspeechNext(data: any = ''): Promise<any> {
        let url: string = `${this.NextSpeech}`;
        let headers: Headers = new Headers({
            'Content-Type': 'application/json'
        });
        return this.http.post(url, data, { headers: headers }).toPromise();
    }

    // For delete operation
    delete(p1, token): Promise<any> {
        let url: string = `${this.BASE_URL}` + p1;
        let headers: Headers = new Headers({
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
        });
        return this.http.delete(url, { headers: headers }).toPromise();
    }

    handleError(error) {
        if (error.status === 401) {


        }
    }
    putResult(p1, data: any = '', token): Promise<any> {
        let url: string = `${this.BASE_URL}` + p1;
        let headers: Headers = new Headers({
            'Content-Type': 'application/json',
            'x-access-token': `${localStorage.getItem('token')}`,

        });
        return this.http.put(url, data, { headers: headers }).toPromise();
    }
    // for get the data
    SignuppostDetail(p1, data: any = ''): Promise<any> {
        let url: string = `${this.BASE_URL}` + p1;
        let headers: Headers = new Headers({
            'Content-Type': 'application/json',
        });
        return this.http.post(url, data, { headers: headers }).toPromise();
    }
    dataShare(data: any = ''): Promise<any> {
        return (data).toPromise();
    }
    Logout() {
        localStorage.removeItem('token');
        localStorage.removeItem('Authid');
        this.router.navigateByUrl('/');
    }
}
